<script>
export default {
  name: 'proyecto',
  props: {
    datos_proy: {},
    hover: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      // hover: false
      wwidth: 0
    }
  },
  created: function() {
    this.wwidth = window.innerWidth;
  },
  methods: {
    ctaLink(link) {
      if (link === 'case') {
        this.$router.push({name: this.datos_proy.nombre});
      } else {
        window.open(this.datos_proy.links.cta);
      }
    }
  }
}
